@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hero {
	display: flex;
	flex-direction: row;
}

.hero_left {
	display: flex;
	flex-direction: column;
	width: 40%;
	justify-content: center;
	align-content: center;
	padding-left: 5%;
}

.hero_right {
	display: flex;
	width: 60%;
}

.hero_right > img {
	width: 100%;
}

.main_text {
	width: 80%;
	font-family: Roboto;
	font-weight: bold;
	line-height: 135px;
	font-size: 144px;
	text-align: left;
	color: #000000;
}

.button {
	float: left;
	width: 300px;
	height: 51px;
}

.button_container {
	display: flex;
	height: 51px;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	color: white;
	background: #1BE9C4;
	border-radius: 50px;
}

.button_container > img {
	height: 31px;
	margin-right: 12px;
}

.button_text {
	font-family: Roboto;
	font-weight: bold;
	font-size: 22px;
	color: #FFFFFF;
}
.description {
	display: flex;
	margin: 150px 0;
}

.description_left {
	width: 50%;
}

.description_right {
	width: 50%;
}

.description_img {
	height: 85%;
	width: 60%;
}

.description_right {
	text-align: left;
	padding-right: 5%;
}

.description_title {
	font-family: Roboto;
	font-weight: bold;
	font-size: 48px;
	color: #212121;
	margin-bottom: 80px;
}

.description_subtitle {
	font-family: Roboto;
	font-weight: bold;
	font-size: 32px;
	color: #000000;
}

.description_text {
	font-family: Roboto;
	font-weight: normal;
	font-size: 24px;
	color: #000000;
}
.courses {
	display: flex;
	padding-right: 3%;
	padding-left: 3%;
}

.courses_titile {
	width: 10%;
}

.courses_cards {
	display: flex;
	justify-content: space-around;
	width: 90%;
}

.courses_card {
	padding-right: 40px;
	padding-left: 40px;
	padding-bottom: 65px;
	padding-top: 65px;
	text-align: left;
	width: 20%;
}

#basics {
	background: linear-gradient(180deg, rgba(2, 171, 243, 0.7) 0%, rgba(24, 90, 157, 0.24) 100%);
	border-radius: 25px;
}

#oop {
	background: linear-gradient(180deg, rgba(255, 0, 92, 0.819) 0%, rgba(168, 207, 228, 0.666) 100%);
	border-radius: 25px;
}

#android {
	background: linear-gradient(180deg, rgba(19, 209, 16, 0.792) 0%, rgba(51, 24, 157, 0.224) 100%);
	border-radius: 25px;
}

.courses_card_title {
	display: flex;
	flex-direction: row;
	padding: 0;
}

.courses_card_title > img {
	align-self: center;
	margin: 0 auto;
	width: 69px;
	height: 69px;
}

.courses_card_title > p {
	font-family: Roboto;
	font-weight: bold;
	margin-bottom: 0 10;
	line-height: 45px;
	font-size: 48px;
	color: #FFFFFF;
}

.courses_card_duration {
	margin: 0;
	font-family: Roboto;
	font-weight: bold;
	font-size: 55px;
	color: #FFFFFF;
}

.courses_card_text {
	font-family: Roboto;
	font-weight: 300;
	font-size: 19px;
	color: #FFFFFF;
}

.courses_title_div {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 10%;
}

.courses_title {
	font-family: Roboto;
	font-weight: bold;
	line-height: 90px;
	font-size: 96px;
	color: #000000;
	-webkit-transform: rotate(-90deg);
	        transform: rotate(-90deg);
	margin: 0;
}
.steps {
	padding-top: 150px;
	padding-bottom: 100px;
}

.title {
	font-family: Roboto;
	font-weight: bold;
	font-size: 64px;
	color: #353535;
}

.steps_sections {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-right: 5%;
	padding-left: 5%;
	height: 800px;
	text-align: left;
	background-position: center;
	background-repeat: no-repeat;
}

.steps_right, .steps_left {
	width: 35%;
}

.step_div {
	margin-bottom: 150px;
}

.step_div_bottom {
	margin: 0 auto;
	width: 45%;
}

.step_number {
	font-family: Roboto;
	font-weight: bold;
	font-size: 48px;
	color: #72E7D1;
	padding-left: 15px;
}

.step_text {
	font-family: Roboto;
	font-weight: bold;
	font-size: 26px;
	color: #5F5F5F;
}
.value {
	padding-right: 5%;
	padding-left: 5%;
	text-align: left;
}

.value_title {
	font-family: Roboto;
	font-weight: bold;
	line-height: 90px;
	font-size: 96px;
	color: #000000;
	width: 50%;
}

.value_divs {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.value_div {
	display: flex;
	flex-direction: column;
	position: relative;
	width: 30%;
	height: 120px;
	padding: 0 10px;
	margin-bottom: 40px;
	background: #FFFFFF;
	box-shadow: 5px 5px 20px rgba(6, 6, 6, 0.25);
	border-radius: 6px;
}

.value_text {
	display: flex;
	height: 100%;
	align-items: center;
	font-family: Roboto;
	font-weight: bold;
	line-height: 17px;
	font-size: 18px;
	color: #000000;
}

.value_icon {
	position: absolute;
	top: -25px;
	width: 50px;
	height: 50px;
}
.register {
	display: flex;
	background: #6544E8;
	padding: 5%;
	justify-content: space-between;
}

.register_form, .register_description {
	width: 40%;
}

.register_description_div {
	display: flex;
}

.register_description_div > img {
	width: 60px;
	height: 60px;
}

.register_description_title {
	padding-left: 20px;
	font-family: Roboto;
	font-weight: bold;
	color: white;
	text-align: left;
}

.register_description_title > h2 {
	margin-top: 10px;
	font-size: 34px;
}

.register_description_title > p {
	font-size: 18px;
}

.register_form {
	padding: 5%;
	background: #FFFFFF;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.35);
	border-radius: 10px;
	text-align: left;
	display: flex;
	flex-direction: column;
}

.register_form > input {
	margin-bottom: 20px;
	height: 45px;
	width: 80%;
	border: 3px solid #6544E8;
	border-radius: 8px;
}

.register_form > h2 {
	font-family: Roboto;
	font-size: 38px;
	color: #000000;
}

.register_form > input::-webkit-input-placeholder {
	padding-left: 5%;
	font-family: Roboto;
	font-weight: 300;
	font-size: 18px;
	color: #929292;
}

.register_form > input::-ms-input-placeholder {
	padding-left: 5%;
	font-family: Roboto;
	font-weight: 300;
	font-size: 18px;
	color: #929292;
}

.register_form > input::placeholder {
	padding-left: 5%;
	font-family: Roboto;
	font-weight: 300;
	font-size: 18px;
	color: #929292;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

